/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: Lato;
  src: url("../fonts/Lato-Regular.eot");
  src: url("../fonts/Lato-Regular.eot") format("embedded-opentype"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.ttf") format("truetype"), url("../fonts/Lato-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Lato;
  src: url("../fonts/Lato-Bold.eot");
  src: url("../fonts/Lato-Bold.eot") format("embedded-opentype"), url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff"), url("../fonts/Lato-Bold.ttf") format("truetype"), url("../fonts/Lato-Bold.svg") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
body {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.02em;
  font-family: Lato, Arial, sans-serif;
}

*, *::before, *::after {
  box-sizing: border-box;
}

.input {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.02em;
  padding: 8px;
  background: none;
  border: 1px solid #fff;
  color: #fff;
  font-family: Lato, Arial, sans-serif;
}
.input:focus {
  outline: 0;
}
.input::placeholder {
  color: #fff;
}

.button {
  transition: 0.3s background-color;
  font-size: 12px;
  line-height: 16px;
  padding: 8px 24px;
  appearance: none;
  background-color: #2666cf;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
}
.button:hover {
  background-color: #3976db;
}

.button-icon {
  transition: 0.3s background-color;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  padding: 8px;
  appearance: none;
  background-color: #f5F2e7;
  border: 1px solid #f5F2e7;
  cursor: pointer;
}
.button-icon:hover {
  background-color: #fefefd;
}

.container-vp-full {
  width: 100vw;
  height: 100vh;
}

.wrapper {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 16px;
}

.todolist-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #f5F2e7;
}

.todo-list {
  display: flex;
  flex-direction: column;
  height: 600px;
  padding: 48px 48px 16px;
  background-color: #2c3333;
  border-radius: 4px;
  color: #fff;
}

.todo-header {
  font-size: 48px;
  line-height: 40px;
  font-weight: bold;
  letter-spacing: 0.06em;
  text-align: center;
  text-transform: uppercase;
}

.todo-add {
  margin: 40px 0;
}

.todo-form {
  display: flex;
  align-items: flex-end;
}
.todo-form .todo-input {
  width: 100%;
}

.todo-items-container {
  overflow-y: auto;
  max-height: 300px;
}
.todo-items-container .todo-item {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  padding: 16px 8px;
  background-color: #f5F2e7;
  color: #000;
  transition: 0.3s background-color;
}
.todo-items-container .todo-item:hover {
  background-color: #fff;
}
.todo-items-container .todo-item-completed {
  background-color: rgba(255, 255, 255, 0.8);
}
.todo-items-container .todo-item-completed .todo-item-content {
  text-decoration: line-through;
}
.todo-items-container .todo-item-content {
  margin-right: auto;
}
.todo-items-container .todo-item-actions {
  margin: -4px;
}
.todo-items-container .todo-task-button {
  margin: 0 4px;
}
.todo-items-container .todo-done-button {
  transition: 0.3s background-color;
  font-size: 12px;
  line-height: 16px;
  padding: 8px 24px;
  appearance: none;
  background-color: #395b64;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
}
.todo-items-container .todo-done-button:hover {
  background-color: #446d77;
}
.todo-items-container .todo-remove-button {
  transition: 0.3s background-color;
  font-size: 12px;
  line-height: 16px;
  padding: 8px 24px;
  appearance: none;
  background-color: #860015;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
}
.todo-items-container .todo-remove-button:hover {
  background-color: #a5001a;
}

.todo-bottom {
  display: flex;
  margin-top: auto;
}
.todo-bottom .todo-counter {
  padding-right: 16px;
  margin-right: auto;
}
.todo-bottom .todo-actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -4px;
}
.todo-bottom .todo-action {
  padding: 0 4px;
}
.todo-bottom .todo-action .todo-action-link {
  color: #fff;
}

.todo-footer {
  font-size: 12px;
  line-height: 16px;
  margin-top: 24px;
  text-align: right;
}